import React from 'react';
import { Input,Modal,Form } from 'antd';
import { connectAlita } from 'redux-alita';
import {addUserlist,getUserlistDetail,editUserlist} from '../../../../api/user'

class AddUser extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible:false,
            userName:'',
            userPwd:'',
            userPwdAgain:'',
            userEmail:'',
            code:'',
            title:'',
            status:'add'
        }
    }
    componentDidMount(){
        this.props.onRef(this);
        this.setState({
            visible:this.props.status
        })
    }

    myName=(status,data)=>{
        this.setState({
            visible:true,
            userName:'',
            userPwd:'',
            userPwdAgain:'',
            userEmail:'',
            code:'',
        });
        if(status+''==='edit'){
            getUserlistDetail(data).then(res=>{
                if(res && res.code+''==='200'){
                    let data = res.data||{};
                    this.setState({
                        ...data,
                        title:'',
                        status:'edit'
                    })
                }
            })
        }else{
            this.setState({
                title:'',
                status:'add'
            })
        }
    };

    ///
    handleOk=(e)=>{
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                let params={
                    userName:this.state.userName,
                    userPwd:this.state.userPwd,
                    userPwdAgain:this.state.userPwdAgain,
                    userEmail:this.state.userEmail,
                    code:this.state.code,
                };
                if(this.state.status==='edit'){
                    params.id = this.state.id||'';
                    editUserlist(params).then(res=>{
                        if(res && res.code+''==='200'){
                            this.setState({
                                userName:'',
                                userPwd:'',
                                userPwdAgain:'',
                                userEmail:'',
                                code:'',
                            },()=>{
                                this.setState({
                                    visible:false
                                },()=>{
                                    this.props.RefreshTable()
                                })
                            })
                        }
                    })
                }else{
                    addUserlist(params).then(res=>{
                        if(res && res.code+''==='200'){
                            this.setState({
                                userName:'',
                                userPwd:'',
                                userPwdAgain:'',
                                userEmail:'',
                                code:'',
                            },()=>{
                                this.setState({
                                    visible:false
                                },()=>{
                                    this.props.RefreshTable()
                                })
                            })
                        }
                    })
                }
            }
        });
    };
    ///
    handleCancel=()=>{
        this.setState({
            userName:'',
            userPwd:'',
            userPwdAgain:'',
            userEmail:'',
            code:'',
        },()=>{
            this.setState({
                visible:false
            })
        })
    }
    getUserName=(data)=>{
        this.setState({
            userName:data.nativeEvent.target.value
        })
    };
    getUserPwd=(data)=>{
        this.setState({
            userPwd:data.nativeEvent.target.value
        })
    };
    getUserPwdAgain=(data)=>{
        this.setState({
            userPwdAgain:data.nativeEvent.target.value
        })
    }
    getEmail=(data)=>{
        this.setState({
            userEmail:data.nativeEvent.target.value
        })
    }

    getCode=(data)=>{
        this.setState({
            code:data.nativeEvent.target.value
        })
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
                md: {
                    span:6
                }
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
                md: {
                    span:18
                }
            },
        };
        return (
            <div>
                <Modal
                    width={'40%'}
                    title={this.state.title}
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    okText=""
                    cancelText=""
                >
                    <div>
                        <Form {...formItemLayout}>
                            <Form.Item label=''>
                                {getFieldDecorator('userName', {
                                    initialValue:this.state.userName,
                                    rules: [
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ],
                                })(<Input allowClear placeholder="" onChange={value=>{this.getUserName(value)}}/>)}
                            </Form.Item>
                            <Form.Item label=''>
                                {getFieldDecorator('password', {
                                    initialValue:this.state.userPwd,
                                    rules: [
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ],
                                })(<Input allowClear placeholder=""  onChange={value=>{this.getUserPwd(value)}}/>)}
                            </Form.Item>
                            <Form.Item label=''>
                                {getFieldDecorator('againPassword', {
                                    initialValue:this.state.userPwdAgain,
                                    rules: [
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ],
                                })(<Input allowClear placeholder=""  onChange={value=>{this.getUserPwdAgain(value)}}/>)}
                            </Form.Item>
                            <Form.Item label="">
                                {getFieldDecorator('email', {
                                    initialValue:this.state.userEmail,
                                    rules: [
                                        {
                                            type: 'email',
                                            message: '',
                                        },
                                        {
                                            required: true,
                                            message: '',
                                        },
                                    ],
                                })(<Input  allowClear placeholder=""  onChange={value=>{this.getEmail(value)}}/>)}
                            </Form.Item>
                            <Form.Item label=""  style={{display:"flex",flexDirection:'row',alignItems:'center'}}>
                                <div style={{display:"flex",flexDirection:'row',alignItems:'center'}}>
                                    <div style={{flex:1}}>
                                        {getFieldDecorator('code', {
                                            initialValue:this.state.code,
                                            rules: [
                                                {
                                                    required: true,
                                                    message: '',
                                                }
                                            ],
                                        })( <Input allowClear placeholder=""  onChange={value=>{this.getCode(value)}}/>)}
                                    </div>
                                    <div style={{marginLeft:'10px',marginTop:'15px',width:'80px'}}>
                                        <p style={{color:'#fff',backgroundColor:'#444',lineHeight:'32px',paddingLeft:'5px',paddingRight:'5px'}}></p>
                                    </div>
                                </div>
                            </Form.Item>
                        </Form>
                    </div>
                </Modal>
            </div>
        )
    }
}

export default connectAlita(['AddUser'])(Form.create()(AddUser));
